<template>
    <div class="directGrouping" v-loading="$store.state.loading">
        <div class="direct_title">
            <div style="color: #4a90e2;">签到分组</div>
            <!--<span style="color: #000;">|</span>-->
        </div>
        <div class="direct_main">
            <h3>参演人员签到列表</h3>
            <div style="float: right;">
                <div v-if="showDrawBtn" style="float: right;">
                    <el-button v-if="actCount < groupLength" type="primary" disabled>抽签</el-button>
                    <el-button v-if="actCount >= groupLength" type="primary" @click="drawBtnClick()">抽签</el-button>
                </div>
                <el-button v-if="!showDrawBtn" type="primary" @click="startDirectDrill()">开始演练</el-button>
                <el-button type="primary" style="padding: 8px 30px;margin-right: 5px;" @click="openBigView()">打开投影页面</el-button>
            </div>
            <div class="should">
                <p>
                    <span>应到人数 {{ count.should }}</span>
                    <span>已到人数 <span style="color: #4a90e2;">{{ count.already }}</span></span>
                    <span>未到人数 <span style="color: #d0021b;">{{ count.should - count.already }}</span></span>
                </p>
                <div class="direct_detail">
                    <div style="font-size: 16px;height: 25px;line-height:25px;margin-bottom: 10px;">参演者</div>
                    <ul>
                        <li v-if="item.character == 1" v-for="(item,index) in signLists" :key="index" :class="item.user_status == '1' ? 'already_sign': ''">
                            <i class="iconfont el-icon-emergency-dagou1"></i>
                            <span>{{ item.user_name }}</span>
                            <span>{{ item.section_one }}{{ item.section_two }}</span>
                        </li>
                    </ul>
                    <div style="font-size: 16px;height: 25px;line-height:25px;margin-bottom: 10px;">点评或评分人</div>
                    <ul>
                        <li v-if="item.character != 1" v-for="(item,index) in signLists" :key="index" :class="item.user_status == '1' ? 'already_sign': ''">
                            <i class="iconfont el-icon-emergency-dagou1"></i>
                            <span>{{ item.user_name }}</span>
                            <span>{{ item.section_one }}{{ item.section_two }}</span>
                        </li>
                    </ul>
                </div>
            </div>
            <h3  v-if="!showDrawBtn"><span v-if="showDrawResult">抽签结果</span></h3>
            <div class="draw_result" v-if="showDrawResult">
                <ul>
                    <li v-for="(item,key) in drawResultLists" :key="key">
                        <div class="groupName">{{ item.name }}</div>
                        <div class="groupList">
                            <p v-for="(group,index) in item.user" :key="index">
                                <span>{{ group.user_name }}</span>
                                <span>{{ group.section_one }}{{ group.section_two }}</span>
                            </p>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
      data(){
          return{
              project_id: '',
              count: {
                  should: '',
                  already: '',
                  not: '',
              },  //人数详情
              signLists: [],      //签到人员列表详情（参演，评分点评）
              drawActLists: [],   //要进行抽签的人（参演）
              drawResultLists: [],//显示抽签结果所有人
              allList: [],        //组长组员的抽签结果
              leaderLists: [],    //抽签结果组长
              showDrawBtn: true,     //显示抽签按钮 还没抽签
              showDrawResult: false,    //显示抽签结果
              groupList: [],      //演练分组
              groupName: '',      //组名（当演练不分组的时候保存）
              timeDraw: '',
              groupLength: 0,       //分组数
              actCount: 0,      //已经签到的参演人员数
          }
      },
      created () {
          this.$store.state.showHeader = '3';
          this.$store.state.loading = true;
          this.project_id = localStorage['drill_project'];
          this.getDrawList(); //获取参演人员列表
      },
      mounted() {
          this.timeDraw = setInterval(()=>{
              if(this.drawResultLists.length != 0){
                  clearInterval(this.timeDraw);
              }
              this.getDrawList(); //查看签到详情
          },5000);
          this.getGroupType();//获取分组详情
      },
      methods:{
          //开始演练
          startDirectDrill(){
              let self = this;
              if(this.groupName){
                  //判断是否在后台保存状态
                  this.$axios.post('/api/the-draw',{
                      design_id: this.project_id
                  }).then((res) => {
                      if(res.data.data[0].user.length == 0){
                          //还没保存
                          this.drawClick();
                          this.$axios.post('/api/draw-lots',{
                              sign: this.allList,
                          }).then(function (response) {
                          })
                      } else {//已经保存状态
                      }
                  }).catch((error)=>{
                  })
              } else {
              }
              this.$axios.post('/api/start',{
                  design_id: this.project_id
              }).then(function (response) {
//                  console.log(response);
                  if(response.data.status == 0){
                      self.$router.push('/direct/start');
                  }
              }).catch(function (error) {
              })
          },
		  //打开大屏
		  openBigView(){
			  let host = window.location.host;
			  window.open('http://' + host + this.$store.state.maxGroup_path);
		  },

          //获取参演人员签到列表
          getDrawList(){
              let self = this;
              this.$axios.post('/api/user-sign',{
                  design_id: this.project_id
              }).then(function (response) {
//                  console.log(response);
                  //获取参演人员  去除导演等
				  let showAll = []; //所有人  将对象数据转为数组（且取出点评人）
                  let arrAct = [];  //参演人员
                  let otherArr = [];  //所有除导演外的
                  if(response.data.status == '0'){
					  //最后一个键值  如果是score_user 有点评人
					  let last = response.data.data["score_user"];
//					  console.log(response.data.data["score_user"]);
//					  console.log(Object.keys(response.data.data)[Object.keys(response.data.data).length-1]);
					  if(last.length == 0){ //没有点评人
						  for(let i in response.data.data){
							  showAll.push(response.data.data[i])
						  }
                      } else {
					  	  //有点评人
                          for(let i in response.data.data){
                              showAll.push(response.data.data[i])
                          }
                          showAll.pop();    //去除点评数组后重新加入数据
                          showAll.push(response.data.data["score_user"][0])
//                          console.log(showAll)
                      }
                      for(let i in showAll){
						  if(showAll[i].character == '1'){  //参演  做抽签
							  arrAct.push(showAll[i])
						  }

						  if(showAll[i].character != '0'){      //参演点评评分  列表显示
							  otherArr.push(showAll[i]);
						  }
                      }
//                      for(let i in response.data.data){
//                          if(response.data.data[i].character == '1'){
//                              arrAct.push(response.data.data[i])
//                          }
//                      }
//                      for(let i in response.data.data){
//                          if(response.data.data[i].character != '0'){
//                              otherArr.push(response.data.data[i]);
//                          }
//                      }
                  }
                  self.signLists = otherArr;//签到人员列表
                  self.drawActLists = arrAct; //要进行抽签的
                  self.count.should = self.signLists.length;
                  let has = 0;
                  let drawHas = 0;  //可以抽签的参演人员数
                  for(let i in self.signLists){
                      if(self.signLists[i].user_status == '1'){
                          has++;
                          if(self.signLists[i].character == '1'){
                          	    drawHas++;
                          }
                      }
                  }
                  self.actCount = drawHas;
                  self.count.already = has;
//                  self.getGroupType();//获取分组详情
              }).catch(function () {
              })
          },
          //获取演练分组
          getGroupType(){
              this.$axios.post('/api/group-start',{
                  design_id: this.project_id
              }).then( (response) => {
                  this.groupLength =response.data.data.length;
                  if(response.data.data){
                      this.groupList = response.data.data;
                      if(response.data.data[0].name == "此演练不分组"){
                          this.showDrawBtn = false;
                          this.groupName = "此演练不分组";
                          this.$store.state.loading = false;
                      } else {
                          //判断是否已经保存了抽签分组
                          this.$axios.post('/api/the-draw',{
                              design_id: this.project_id
                          }).then((res)=>{
//                              console.log(res);
                              if(res.data.data[0].user.length == 0){
                                  //还没进行抽签
                                  this.showDrawResult = false;
                                  this.showDrawBtn = true;
                              } else {
                                  //抽签分组完成
                                  this.showDrawResult = true;
                                  this.showDrawBtn = false;
                                  //获取分组抽签的数据 并显示
                                  let drawArr = res.data.data;
                                  for(let x in res.data.data){
                                      for(let y in res.data.data[x].user){
                                          drawArr[x].user[y].section_one = drawArr[x].user[y].section.section_one;
                                          drawArr[x].user[y].section_two = drawArr[x].user[y].section.section_two;
                                          drawArr[x].user[y].user_name = drawArr[x].user[y].name;
                                      }
                                  }
                                  this.drawResultLists = drawArr;
                              }
                              this.$store.state.loading = false;
                          }).catch((err)=>{
                          })
                      }
//                      this.$store.state.loading = false;
                  }
              }).catch(function (error) {
              })
          },

          //进行抽签分组
          drawClick(){
              //抽签人员列表
              let arr1 = [];
              for(let i in this.drawActLists){
                  if(this.drawActLists[i].user_status == 1){
                      arr1.push(this.drawActLists[i]);
                  }
              }
              let arr2 = this.groupList;  //小组列表
              let count = Math.ceil(arr1.length/arr2.length);  //每组多少个
              for(let i=0;i<arr2.length;i++){
                  arr2[i].user = [];
                  for(let x=0;x<arr1.length;x++){
                      let a = count*i;
                      let b = count*(i+1);
                      if( Number(a) <= Number(x) && Number(x) < Number(b) ){
                          arr2[i].user.push(arr1[x])
                      }
                  }
              }
              this.drawResultLists = arr2;  //抽签结果显示数据
              //获取传后台的数据
              let arr = [];           //保存抽签后每组的人员id和组id
              for(let i in this.drawResultLists){
                  for(let x in this.drawResultLists[i].user){
                      arr.push({
                          user_id: this.drawResultLists[i].user[x].user_id,
                          group_id: this.drawResultLists[i].id,
                      })
                  }
              }
              this.allList = arr;     //所有组员
              //多组时需要传组长数组
              let arrLeader = [];     //保存抽签后每组的组长
              for(let i in this.drawResultLists ){
                  arrLeader.push({
                      group_id: this.drawResultLists[i].id,
                      user_id: this.drawResultLists[i].user[0].user_id
                  })
              }
              this.leaderLists = arrLeader;
          },
          //抽签按钮
          drawBtnClick(){
              this.$store.state.loading = true;
              this.drawClick(); //进行分组
              let self = this;
              this.$axios.post('/api/draw-lots',{
                  sign: this.allList,
                  group_user: this.leaderLists
              }).then(function (response) {
//                  console.log(response);
                  if(response.data.status == '0'){
                      self.showDrawBtn = false;
                      self.showDrawResult = true;
                  } else {
                      self.showDrawBtn = true;
                      self.showDrawResult = false;
                  }
                  self.$store.state.loading = false;
              }).catch(function (error) {
              });
          },
      }
  }
</script>
<style lang="less" rel="stylesheet/less">
  .directGrouping{
      .direct_title{
          padding-left: 50px;
          height: 40px;
          background-color: #f1f1f1;
          line-height: 40px;
          div{
              display: inline-block;
              padding: 0 10px;
          }
          .isDirect{
              color: #4a90e2;
          }
      }
      .direct_main{
          /*width: 1125px;*/
          margin-top: 10px;
          background-color: #f1f1f1;
          margin-left: 46px;
          margin-right: 55px;
          padding: 15px 20px 48px 35px;
          h3{
              font-size: 16px;
              text-align: left;
              color: #000000;
              font-weight: normal;
              display: inline-block;
          }
          .el-button{
              float: right;
              height: 30px;
              padding: 8px 40px;
          }
          //应到人
          div.should{
              margin-top: 17px;
              background-color: #fff;
              padding: 10px 0 20px 20px;
              margin-bottom: 17px;
              p{
                  margin-bottom: 20px;
                  span{
                    height: 22px;
                    font-size: 16px;
                    text-align: left;
                    color: #000000;
                  }
                  >span{
                      margin-right: 40px;
                  }
              }
              /***签到详情***/
              .direct_detail{
                  ul{
                      li{
                          font-size: 16px;
                          text-align: left;
                          height: 22px;
                          line-height: 22px;
                          display: inline-block;
                          margin-bottom: 20px;
                          color: #9b9b9b;
                          width: 25%;
                          i{
                              color: #9b9b9b;
                              font-size: 20px;
                          }
                          span{
                              width: 80px;
                              display: inline-block;
                          }
                          span:last-child{
                              float: right;
                              width: 160px;
                              text-align: left;
                              height: 22px;
                              display: -webkit-box;
                              -webkit-box-orient: vertical;
                              -webkit-line-clamp: 1;
                              overflow: hidden;
                          }
                      }
                      .already_sign{
                          color: #000000;
                          i{
                              color: #4a90e2;
                          }
                      }
                  }
              }
          }
          //抽签结果
          .draw_result{
              margin-top: 14px;
              ul{
                  li{
                      display: inline-block;
                      vertical-align: top;
                      background-color: #fff;
                      font-size: 16px;
                      line-height: 2.12;
                      margin-top: 10px;
                      padding: 14px 0 22px;
                      margin-right: 17px;
                      margin-bottom: 20px;
                      width: 30%;
                      .groupName{
                          text-align: center;
                          color: #4a4a4a;
                          font-weight: 500;
                      }
                      .groupList{
                          span{
                              text-align: left;
                              display: inline-block;
                              width: 70px;
                              margin-left: 30px;
                          }
                          span:last-child{
                              width: auto;
                              margin-left: 0;
                          }
                      }
                  }
              }
          }
      }
  }
</style>



